<template>
  <div id="app">
    <el-card shadow="always">
      <div style="display: flex;justify-content: space-around;"><b>圣井吃什么？</b></div>
      <el-divider></el-divider>
      <div  style="display: flex;justify-content: space-around;">{{kkk}}</div>
  </el-card>
  <div style="height:20px ;"></div>
     <el-button type="success" @click="start">{{btu}}</el-button> <el-button @click="setting" type="primary" icon="el-icon-edit" circle></el-button>
     <el-drawer
  title="设置"
  :visible.sync="drawer"
  direction="btt">
  <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
  <div style="margin: 15px 0;"></div>
  <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
    <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
  </el-checkbox-group>
</el-drawer>
<el-dialog
  title="关于"
  :visible.sync="dialogVisible"
  width="80%"
  :before-close="handleClose">
  <span style="display: flex;justify-content: space-around;"><b>解决人类的世纪难题！吃什么不用愁！</b></span>
  <div style="height:30px;"></div>
  <span style="display: flex;justify-content: space-around;" >竟何@lylelove</span>
  <span  style="display: flex;justify-content: space-around;">V0.11</span>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
<el-button @click="gourl"  type="info" center="true"  style="position: fixed ; bottom: 60px ; right: 10px;" icon="el-icon-s-comment" circle></el-button>
<el-button type="info" center="true" @click="openinfo" style="position: fixed ; bottom: 10px ; right: 10px;" icon="el-icon-info" circle></el-button>
  </div>
</template>

<script>
  const cityOptions = ['三餐三楼','三餐二楼','三餐一楼','二餐二楼','二餐一楼','二餐民族餐厅','一餐一楼','一餐二楼'];
export default {
  name: 'App',
  components: {
    
  },
  data() {
    return {
      show_1:false,
       drawer: false,
       dialogVisible:false,
				list: [{
										icon: 'icon-commentdots-fill',
										color: 'bg-gradient-orange-accent'
									},
									{
										icon: 'icon-info-circle',
										color: 'bg-gradient-blue-accent'
									}
								],
				fisrst:true,
				where:'三餐三楼',
				what:'自选餐',
				kkk:'解决人类的世纪难题！吃什么不用愁！',
				btu:'开始',
				where_list:['三餐三楼','三餐二楼','三餐一楼','二餐二楼','二餐一楼','二餐民族餐厅','一餐一楼','一餐二楼'],
				eat_list:[
					{
						'where':'三餐三楼',
						'eat':['自选餐','黄焖鸡']
					},
					{
						'where':'三餐二楼',
						'eat':['饺子']
					},
					{
						'where':'三餐一楼',
						'eat':['面条','牛肉饭','炒鸡饭','自选餐','炒饭']
					},
					{
						'where':'二餐二楼',
						'eat':['烤盘饭','烤鸭饭','肘子面','炸串']
					},
					{
						'where':'二餐一楼',
						'eat':['包子','煎饺','肘子面']
					},
					{
						'where':'二餐民族餐厅',
						'eat':['大盘鸡','拉面','炒饭']
					},
					{
						'where':'一餐一楼',
						'eat':['炸鸡','面条']
					},
					{
						'where':'一餐二楼',
						'eat':['猪脚饭','牛杂面','羊杂面']
					},
					
				],
        checkAll: false,
        checkedCities: ['三餐三楼','三餐二楼','三餐一楼','二餐二楼','二餐一楼','一餐一楼','一餐二楼'],
        cities: cityOptions,
        isIndeterminate: true
    }
  },
  methods:{
      start(){
				if(this.fisrst){
					this.fisrst=false;
					this.btu='再试一次'
				}
				var temp1=Math.round(Math.random()*(this.where_list.length-1))
				var temp_where=this.where_list[temp1];
				this.where=temp_where;
				for(var i=0;i<this.where_list.length;i++){
					if(this.eat_list[i]['where']==temp_where){
						var temp2=Math.round(Math.random()*(this.eat_list[i]['eat'].length-1))
						this.what = this.eat_list[i]['eat'][temp2];
					}else{
						continue;
					}
				}
        if(this.checkedCities.indexOf(this.where)>=0){
          this.kkk='去'+this.where+'吃'+this.what
        }else{
          this.start()
        }
        
			},
      setting(){
        this.drawer=true
      },
      handleCheckAllChange(val) {
        this.checkedCities = val ? cityOptions : [];
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      openinfo(){
        this.dialogVisible=true
      },
      gourl(){
        window.location.href='https://www.lylelove.top/eatWhat/message'
      }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
